					<p>
						{'SoundsVinyl is a site about music. What underpins our attraction to music – perhaps all art – is the emotion and the memories that we attach to it. Music is both a communal and a solitary experience. We can share it together with thousands of others or experience it on our own on repeat until we drain all feeling from what we are playing.'}
					</p>

					<p>
						{'The sounds highlighted on this site may offer you a different, brief glimpse of sublimity; an emotional epiphany which popular culture is capable of providing just as much as high art can.'}
